@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2b2b2b;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-form {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 2em 0;
  width: 100%;
}

.search-input {
  border: 2px inset rgba(0, 0, 0, 0.3);
  border-radius: 8px 0 0 8px;
  height: 2.5em;
  padding: 0 0.5em;
  width: 50%;
}

.search-input:focus-visible {
  background-color: #f7f7f7;
  outline: none;
}

.search-button {
  background: #e2e2e2;
  border: none;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
  display: grid;
  height: 2.5em;
  place-items: center;
  width: 2.5em;
}

.search-button:active {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.filter-item {
  display: flex;
  font-size: 0.9em;
  padding: 0.8em 2em;
  position: relative;
  transition: background-color 0.1s ease;
}

.filter-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.filter-item:after {
  background: rgba(0, 0, 0, 0.2);
  bottom: 0;
  content: '';
  height: 1px;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  width: 75%;
}

.item-selector {
  display: none;
}

.selected {
  background-color: rgba(0, 0, 0, 0.1);
}

.sidebar {
  background: #e2e2e2;
  height: 100vh;
  overflow: scroll;
  padding-top: 1em;
  position: fixed;
  width: 20vw;
}

.filter {
  list-style: none;
}

.cart-link {
  background: white;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  color: #2b2b2b;
  display: grid;
  font-size: 2em;
  height: 1.5em;
  place-items: center;
  position: fixed;
  right: 2em;
  top: 0.5em;
  width: 1.5em;
  z-index: 2;
}

.cart-link svg {
  -webkit-animation: cart-animation 0.2s ease-in-out;
          animation: cart-animation 0.2s ease-in-out;
}

.cart-link span {
  background: #888;
  border-radius: 50%;
  bottom: 0;
  color: white;
  display: grid;
  font-size: 0.4em;
  height: 1.7em;
  left: 100%;
  place-items: center;
  position: absolute;
  text-decoration: none;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 1.7em;
}

@-webkit-keyframes cart-animation {

  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes cart-animation {

  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

.product-card {
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  justify-content: space-between;
  position: relative;
  text-align: center;
  width: 20em;
}

.product-card:hover {
  outline: 3px solid rgba(0, 0, 0, 0.1);
}

.product-info {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  height: 100%;
  justify-content: space-between;
  padding: 1em 1.5em;
}

.product-image {
  align-self: center;
  margin-top: 1em;
  width: 60%;
}

.product-detail-link {
  font-size: 0.7em;
}

.cart-button {
  background: #e2e2e2;
  border: none;
  border-radius: 0 0 5px 5px;
  color: #2b2b2b;
  cursor: pointer;
  height: 3.5em;
  margin-top: auto;
  width: 100%;
}

.cart-button:active {
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}

.free-shipping {
  color: rgba(0, 0, 0, 0.6);
  left: 1em;
  pointer-events: none;
  position: absolute;
  top: 1em;
}

.shipping-icon {
  font-size: 1.5em;
}

.free-shipping > p {
  font-size: 0.6em;
}

.at-cart {
  color: rgb(24, 122, 0);
  pointer-events: none;
  position: absolute;
  right: 1.5em;
  top: 1.5em;
}

.at-cart-icon {
  font-size: 1.5em;
}

.products {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 3em;
  gap: 3em;
  justify-content: center;
  padding: 1em 3em 2em;
}

.cart-item {
  align-items: center;
  display: flex;
  grid-gap: 1em;
  gap: 1em;
  justify-content: center;
  padding: 1em 2em;
  position: relative;
}

.cart-item:after {
  background-color: rgba(0, 0, 0, 0.2);
  bottom: 0;
  content: '';
  height: 1px;
  position: absolute;
  width: 100%;
}

.remove-item {
  background: none;
  border: none;
  color: red;
  font-size: 1.2em;
}

.item-image {
  width: 6em;
}

.item-title {
  flex-basis: 30em;
  text-align: left;
}

.quantity-container {
  align-items: stretch;
  display: flex;
}

.quantity-container button {
  background: white;
  border: 1px solid rgba(0, 0, 0, 0.2);
  display: grid;
  font-size: 1em;
  padding: 0.2em 0.5em;
  place-items: center;
}

.quantity-container button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.quantity-container button svg {
  pointer-events: none;
}

.quantity-container p {
  background: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  display: grid;
  padding: 0.2em;
  place-items: center;
  width: 2.5em;
}

.item-price {
  flex-basis: 10em;
  text-align: end;
}

.home-icon {
  color: #2b2b2b;
  font-size: 2em;
  left: 2em;
  position: absolute;
  top: 0.5em;
}

.cart-content {
  text-align: center;
}

.cart-items, .product-details {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-top: 5em;
  padding-bottom: 3em;
  width: 60%;
}

.total-price {
  align-self: flex-end;
  margin-right: 2em;
  padding: 2em 0;
}

.purchase {
  align-self: center;
  background: #e2e2e2;
  border-radius: 5px;
  color: #2b2b2b;
  display: flex;
  font-size: 0.8em;
  font-weight: 700;
  padding: 1em 2em;
  text-decoration: none;
  width: -webkit-max-content;
  width: max-content;
}

.purchase:hover {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.evaluation-form {
  display: flex;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  margin: 2em 0;
}

.evaluation-form > label {
  font-size: 1.2em;
}

.email-rating {
  align-items: center;
  display: flex;
  grid-gap: 3em;
  gap: 3em;
}

.email-rating > input {
  padding: 0.5em;
}

.evaluation-form > input, .evaluation-form > textarea {
  display: block;
  padding: 0.5em;
}

.evaluation-form > textarea {
  height: 10em;
  resize: none;
}

.evaluation-form > button {
  background: #e2e2e2;
  border: none;
  border-radius: 5px;
  color: #2b2b2b;
  cursor: pointer;
  display: flex;
  font-weight: 700;
  margin-top: 0.5em;
  padding: 0.8em 1.5em;
  width: -webkit-max-content;
  width: max-content;
}

.evaluation {
  display: flex;
  flex-direction: column;
  font-size: 1.3em;
  grid-gap: 2em;
  gap: 2em;
  padding: 1.5em 1em;
  position: relative;
}

.evaluation:after {
  background: rgba(0, 0, 0, 0.2);
  bottom: 0;
  content: '';
  height: 1px;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  width: 102%;
}

.email-rating {
  align-items: center;
  display: flex;
  grid-gap: 2em;
  gap: 2em;
  justify-content: space-between;
}

.email {
  font-weight: 700;
}

.product-details {
  font-size: 0.7em;
  grid-gap: 2em;
  gap: 2em;
}

.product-details > button {
  background: #e2e2e2;
  border: none;
  border-radius: 5px;
  color: #2b2b2b;
  cursor: pointer;
  display: flex;
  font-weight: 700;
  padding: 0.8em 1.5em;
  width: -webkit-max-content;
  width: max-content;
}

.product-details button:hover {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.product-price {
  font-size: 1.5em;
}

.product-main-info {
  display: flex;
}

.product-main-info img {
  width: 30%;
}

.product-attributes {
  display: flex;
  flex-direction: column;
  font-size: 1.3em;
  grid-gap: 1em;
  gap: 1em;
  padding-left: 3em;
  width: 70%;
}

.product-attributes p {
  font-weight: 700;
}

.product-attributes span {
  font-weight: 500;
}

.purchase-form {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -2em;
}

.name {
  grid-area: name;
}

.cpf {
  grid-area: cpf;
}

.email {
  grid-area: email;
}

.phone {
  grid-area: phone;
}

.cep {
  grid-area: cep;
}

.address {
  grid-area: address;
}

.customer-info {
  display: grid;
  grid-gap: 0.8em;
  gap: 0.8em;
  grid-template-areas: 'name cpf email phone'
    'cep address address address';
  padding: 1em 0;
}

.customer-info-header {
  align-self: flex-start;
}

.customer-info > input {
  padding: 0.5em;
}

.purchase-form > button {
  align-self: center;
  background: #e2e2e2;
  border: none;
  border-radius: 5px;
  color: #2b2b2b;
  cursor: pointer;
  display: flex;
  font-size: 0.8em;
  font-weight: 700;
  margin-top: 1em;
  padding: 1em 2em;
  text-decoration: none;
  width: -webkit-max-content;
  width: max-content;
}

.purchase-form > button:hover {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.purchase-screen-item {
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr 1fr;
  justify-items: center;
  position: relative;
}

.purchase-screen-item:after {
  background: rgba(0, 0, 0, 0.2);
  bottom: -1em;
  content: '';
  height: 1px;
  position: absolute;
  width: 100%;
}

.purchase-screen {
  display: flex;
  flex-direction: column;
  grid-gap: 2em;
  gap: 2em;
  margin: 0 auto;
  margin-top: 3em;
  width: 70%;
}

.total-price {
  text-align: end;
}

.main-content {
  margin-left: 20vw;
  text-align: center;
}

